@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&family=Nerko+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:white;font-size:18px;font-family:'Montserrat', sans-serif;background-color:black}body .App{display:flex;flex-direction:column;align-items:center;width:100%;height:100%}body .App #markdown-prev{margin:0;width:100%;text-align:center;background-color:#510080;color:white;padding:20px}body .App .editor{display:flex;flex-direction:column;align-items:center;width:100%;height:50%;font-size:30px;padding:10px;border:none}body .App .editor #editor{background-color:purple;border:1 px solid white;color:white;border-radius:12px;padding:10px}body .App .editor #typehere{color:white;background-color:black;width:100%;padding:20px;text-align:center}body .App .preview{display:flex;flex-direction:column;align-items:center;width:100%;height:50%;font-size:30px;padding:10px;background-color:silver}body .App .preview #prev{color:black;border-bottom:1px solid grey;padding:20px;text-align:center}body .App .preview #preview{font-size:18px;border:1px solid black;padding:10px;background-color:white;border-radius:12px}

